@import "https://fonts.googleapis.com/css2?family=Chakra+Petch:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&family=Play:wght@400;700&family=Roboto:wght@300;400;500;700&family=Silkscreen&display=swap";
:root {
  --primary: #fff;
  --accent: #007200;
  --terminal: #636b71;
  --terminal-bg: #0c0c0c;
}

body {
  color: #fff;
  background: #000;
  margin: 0;
  font-family: Chakra Petch, sans-serif;
}

a {
  color: #fff;
  text-decoration: none;
}

.wrapper {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  min-height: 100svh;
  font-size: 1.5rem;
  display: flex;
  position: relative;
}

.wrapper > div {
  background-color: var(--terminal-bg);
  border: 3px solid var(--terminal);
  border-top: 20px solid var(--terminal);
  min-width: 500px;
}

.profile-desc {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin: 25px 0 0;
  display: flex;
}

.profile-desc :first-child {
  background-color: var(--terminal-bg);
  border-top: 4px solid var(--terminal);
  border-bottom: 4px solid var(--terminal);
  border-left: 4px solid var(--terminal);
  border-right: 4px solid var(--terminal);
  width: 100px;
}

.profile-desc :last-child {
  display: inherit;
}

.quote {
  background-color: var(--terminal-bg);
  border-top: 3px solid var(--terminal);
  border-bottom: 0px solid var(--terminal);
  border-left: 0px solid var(--terminal);
  border-right: 0px solid var(--terminal);
  text-align: center;
  margin: 25px 0 0;
}

.pages {
  grid-template-rows: repeat(3, 60px);
  grid-template-columns: repeat(2, 200px);
  grid-template-areas: "dashboard dashboard"
                       "projects gallery"
                       "games games";
  place-content: center;
  padding: 1rem 0;
  display: grid;
}

.pages > a:first-child {
  grid-area: dashboard;
}

.pages > a:nth-child(2) {
  grid-area: projects;
}

.pages > a:nth-child(3) {
  grid-area: gallery;
}

.pages > a:nth-child(3):hover {
  cursor: not-allowed;
  border: none;
}

.pages > a:last-child {
  grid-area: games;
}

.pages > a:last-child:hover {
  cursor: not-allowed;
  border: none;
}

.pages > a {
  margin: auto;
  padding: 5px;
}

.pages > a:hover {
  border: 1px solid #fff;
}

.socials {
  grid-template-rows: repeat(3, 40px);
  grid-template-columns: repeat(3, 150px);
  grid-template-areas: "link link link"
                       "link link link"
                       "id id id";
  place-content: center;
  display: grid;
}

.socials :last-child {
  grid-area: id;
}

.socials > * {
  margin: auto;
}

.socials > a:hover {
  text-decoration: underline;
}

#msg {
  min-height: 2rem;
  font-weight: bold;
}

::-webkit-scrollbar {
  width: .3vmax;
  height: .3vmax;
}

::-webkit-scrollbar-corner {
  background: none;
}

::-webkit-scrollbar-thumb {
  background-color: var(--terminal);
}

@media screen and (width >= 350px) and (width <= 470px) {
  .wrapper {
    font-size: 1rem;
  }

  .wrapper > div {
    min-width: 90vw;
  }

  .profile-desc {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    margin: 25px 0 0;
    display: flex;
  }

  .profile-desc :first-child {
    background-color: var(--terminal-bg);
    border-top: 4px solid var(--terminal);
    border-bottom: 4px solid var(--terminal);
    border-left: 4px solid var(--terminal);
    border-right: 4px solid var(--terminal);
    width: 100px;
  }

  .profile-desc :last-child {
    display: inherit;
  }

  .quote {
    text-wrap: balance;
    word-wrap: break-word;
    max-width: 90vw;
    font-size: 1.5rem;
  }

  .pages {
    grid-template-rows: repeat(3, 60px);
    grid-template-columns: 10rem 10rem;
    font-size: 1.4rem;
  }

  .socials {
    grid-template-rows: repeat(3, 40px);
    grid-template-columns: repeat(3, 100px);
    font-size: 1.2rem;
  }
}

